<template>
	<view v-if="goodsdetail.goods_name" class="goods-detail-container">
		<!--轮播图区域-->
		<swiper :indicator-dots="true" autoplay="true" :interval="2000" :duration="1000" :circular="true">
			<swiper-item v-for="(item,index) in goodsdetail.pics" :key="index">				
					<image :src="item.pics_big"  @click="preview(i)"></image>
			</swiper-item>
		</swiper>	
		<!--商品信息区域-->
		<view class="goods-info">
			<!--商品价格区域-->
			<view class="goods-price">
				￥{{goodsdetail.goods_price}}
			</view>
			<!-- 信息主体区域 -->
			  <view class="goods-info-body">
			    <!-- 商品名称 -->
			    <view class="goods-name">{{goodsdetail.goods_name}}</view>
			    <!-- 收藏 -->
			    <view class="favi">
			      <uni-icons type="star" size="18" color="gray"></uni-icons>
			      <text>收藏</text>
				</view>
			</view>
			<!-- 运费 -->
			<view class="yf">快递：免运费</view>		
		</view>
		<rich-text :nodes="goodsdetail.goods_introduce"></rich-text>
		<!-- 商品导航组件 -->
		<view class="goods_nav">
		  <!-- fill 控制右侧按钮的样式 -->
		  <!-- options 左侧按钮的配置项 -->
		  <!-- buttonGroup 右侧按钮的配置项 -->
		  <!-- click 左侧按钮的点击事件处理函数 -->
		  <!-- buttonClick 右侧按钮的点击事件处理函数 -->
		  <uni-goods-nav :fill="true" :options="options" :buttonGroup="buttonGroup" @click="onClick" @buttonClick="buttonClick" />
		</view>
	</view>
</template>

<script>
	import {mapState,mapMutations,mapGetters} from 'vuex'
	export default {
		computed:{
			// 调用 mapState 方法，把 m_cart 模块中的 cart 数组映射到当前页面中，作为计算属性来使用
			// ...mapState('模块的名称', ['要映射的数据名称1', '要映射的数据名称2'])
			...mapState('m_cart',[]),
			...mapGetters('m_cart',['total'])
		},
		watch:{
			 total: {
			      // handler 属性用来定义侦听器的 function 处理函数
			      handler(newVal) {
					  //找到res对象，是text为购物车的对象然后对其info复制
			         const findResult = this.options.find(x => x.text === '购物车')
			         if (findResult) {
			            findResult.info = newVal
			         }
			      },
			      // immediate 属性用来声明此侦听器，是否在页面初次加载完毕后立即调用
			      immediate: true
			   }
		},
		data() {
			return {
				goodsdetail:{},
				// 左侧按钮组的配置对象
				options: [{
				    icon: 'shop',
				    text: '店铺'
				}, {
				    icon: 'cart',
				    text: '购物车',
				    info: 0
				}],
				// 右侧按钮组的配置对象
				buttonGroup: [{
				    text: '加入购物车',
				    backgroundColor: '#ff0000',
				    color: '#fff'
				},
				{
				    text: '立即购买',
				    backgroundColor: '#ffa200',
				    color: '#fff'
				}
				]
			};
		},
		
		//options是指从其他页面跳转携带的数据
		onLoad(options) {
		  // 获取商品 Id
		  const goods_id = options.goods_id
		  // 调用请求商品详情数据的方法
		  this.getGoodsDetail(goods_id)
		},
		
		methods: {
			 ...mapMutations('m_cart', ['addToCart']),
		  // 定义请求商品详情数据的方法
		  async getGoodsDetail(goods_id) {
		    const { data: res } = await uni.$http.get('/api/public/v1/goods/detail', { goods_id })
		    if (res.meta.status !== 200) return uni.$showMsg()
		    // 为 data 中的数据赋值
			// 使用字符串的 replace() 方法，为 img 标签添加行内的 style 样式，从而解决图片底部空白间隙的问题
			res.message.goods_introduce = res.message.goods_introduce.replace(/<img /g, '<img style="display:block;" ')
		    this.goodsdetail = res.message
		  },
		  
		  // 实现轮播图的预览效果
		  preview(i) {
		    // 调用 uni.previewImage() 方法预览图片
		    uni.previewImage({
		      // 预览时，默认显示图片的索引
		      current: i,
		      // 所有图片 url 地址的数组
		      urls: this.goodsdetail.pics.map(x => x.pics_big)
		    })
		  },
		  
		  // 左侧按钮的点击事件处理函数
		  onClick(e) {
		    if (e.content.text === '购物车') {
		      // 切换到购物车页面
		      uni.switchTab({
		        url: '/pages/cart/cart'
		      })
		    }
		  },
		  // 右侧按钮的点击事件处理函数
		  buttonClick(e) {
			  
		     // 1. 判断是否点击了 加入购物车 按钮
		     if (e.content.text === '加入购物车') {
		  
		        // 2. 组织一个商品的信息对象
		        const goods = {
		           goods_id: this.goodsdetail.goods_id,       // 商品的Id
		           goods_name: this.goodsdetail.goods_name,   // 商品的名称
		           goods_price: this.goodsdetail.goods_price, // 商品的价格
		           goods_count: 1,                           // 商品的数量
		           goods_small_logo: this.goodsdetail.goods_small_logo, // 商品的图片
		           goods_state: true                         // 商品的勾选状态
		        }		  
		        // 3. 通过 this 调用映射过来的 addToCart 方法，把商品信息对象存储到购物车中
		        this.addToCart(goods)
		  
		     }
		  }
		}
	}
</script>

<style lang="scss">
swiper {
  height: 750rpx;

  image {
    width: 100%;
    height: 100%;
  }
}
.goods-info{
	padding: 10px;
	padding-right: 0;
	.goods-price{
		font-size: 30px;
		color: #c00000;
		padding: 10px 0;
	}
	.goods-info-body{
		display: flex;
		justify-content: space-between;
		.goods-name{
			font-size: 13px;
			margin-right: 10px;
		}
		.favi{
			width: 120px;
			font-size: 12px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			border-left: 1px solid #efefef;
			color: gray;
		}
	}
	.yf{
		font-size: 12px;
		color: gray;
		padding: 10px 0;
	}
}
.goods-detail-container {
  // 给页面外层的容器，添加 50px 的内padding，
  // 防止页面内容被底部的商品导航组件遮盖
  padding-bottom: 50px;
}

.goods_nav {
  // 为商品导航组件添加固定定位
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
</style>
